//   

import AppLayout from '../components/AppLayout'; 
import Head from 'next/head'
import React from 'react'
import PropTypes from 'prop-types'

//   _app.js . 
// redux state      state   
// _app.js redux    . 

//--react, redux  
import {Provider} from 'react-redux'; //redux state ,    state    . 
import reducer from '../reducers'; 
import {createStore, compose, applyMiddleware} from 'redux'
import withRedux from 'next-redux-wrapper'


//  
import createSagaMiddleware from 'redux-saga';
import rootSaga from '../sagas';

const NodeBird = ({Component,store}) =>{
                    // next  props

        return (
        <Provider store={store}> {/* store redux state.  store   state  . */}
            <div>
            <Head>
                <title>NodeBird</title>
                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/antd/3.16.2/antd.min.css"/>
            </Head>
            <AppLayout >
                <Component />
            </AppLayout>  
            </div>
        </Provider>

        )

}
NodeBird.propTypes = {
    Component: PropTypes.elementType,
    store    : PropTypes.object, 
}

//  
/*
hello(Component); 
const hello = (Component) => ()=>{
    return (
        <Component good="i'm a good person"/>
    )
}
*/

//  ... 
export default withRedux((initialState,options)=>{

    const sagaMiddleware = createSagaMiddleware();
    const middleWares = [sagaMiddleware];
                     
                      
    const enhancer =  process.env.NODE_ENV ==='production' 
                                ? compose(
                               
                                applyMiddleware(...middleWares),
                    
                                )
                                : compose(
                               
                                    applyMiddleware(...middleWares),
                                    !options.isServer && window.__REDUX_DEVTOOLS_EXTENSION !== 'undefined'? window.__REDUX_DEVTOOLS_EXTENSION__() : (f)=>f,
                                    // REDUX DEVTOOLS  
                                    //window  (__REDUX_DEVTOOLS_EXTENSION) , __REDUX_DEVTOOLS_EXTENSION__()    
    
                                    )
    const store = createStore(reducer,initialState,enhancer); 
    sagaMiddleware.run(rootSaga);
    return store; 
})(NodeBird); 