import {all ,fork, takeLatest, call, put }from 'redux-saga/effects'; 
import { LOG_IN, LOG_IN_SUCCESS, LOG_IN_FAILURE } from '../reducers/user';

//call :    
//fort :    
//put  :  , dispatch

function* loginAPI(){
//   

}

function* login(){

    try{
        
        yield call(loginAPI); 
        yield put({
            type: LOG_IN_SUCCESS,
        })

    }catch(e){
        console.error(e); 
        yield put({
            type:LOG_IN_FAILURE,
        });
    }

}

function* watchLogin(){
    yield takeLatest(LOG_IN,login); 
          //takeLatest LOG_IN  dispatch  
          // dispatch  login  . 

}

export default function* userSaga() {

 yield all([
     fork(watchLogin),
 ]); 

}